// /app/shop/category/[category]/[subcategory]/page.tsx
"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";

interface Product {
  id: number;
  name: string;
  image: string;
  price: string;
}

export default function SubcategoryPage() {
  const params = useParams<{ category: string; subcategory: string }>(); // âœ… TypeScript Fix
  const category = params?.category || "";
  const subcategory = params?.subcategory || "";
  const [products, setProducts] = useState<Product[]>([]);

  useEffect(() => {
    async function fetchProducts() {
      try {
        // ðŸ”¹ Backend à¤¸à¥‡ data à¤²à¤¾à¤¨à¥‡ à¤•à¤¾ placeholder
        const data: Product[] = [
          { id: 1, name: `${subcategory} Product 1`, image: "/sample1.jpg", price: "$60" },
          { id: 2, name: `${subcategory} Product 2`, image: "/sample2.jpg", price: "$80" },
        ];
        setProducts(data);
      } catch (error) {
        console.error("Error fetching products:", error);
      }
    }
    if (category && subcategory) fetchProducts();
  }, [category, subcategory]);

  return (
    <div className="container mx-auto p-6">
      <h1 className="text-3xl font-bold">Subcategory: {subcategory}</h1>
      <div className="grid grid-cols-3 gap-6 mt-6">
        {products.map((product) => (
          <div key={product.id} className="border p-4 rounded-md shadow-md">
            <img src={product.image} alt={product.name} className="w-full h-48 object-cover" />
            <h2 className="text-xl mt-2">{product.name}</h2>
            <p className="text-gray-600">{product.price}</p>
          </div>
        ))}
      </div>
    </div>
  );
}

